

.lists{
  width: 95%;
  height: 50px;
  background-color: #fff;
  border-radius: 2px;

  .list_left{
    float: left;
    width: 15%;
    height: 50px;
    // background-color: rgb(204, 89, 89);
    display: flex;
    justify-content: center;
    align-items: center;

    .bookbg{
      width: 80%;
      height: 90%;
      background-image: url(../../../assets/noImg.jpg);
      background-size: 100% 100%;
    }
  }

  .list_center{
    float: left;
    width: 70%;
    height: 50px;
    // background-color: rgb(75, 167, 61);

    .p_name{
      height: 30px;
      line-height: 30px;
      font-size: 16px;
      overflow: hidden;
    }
    .p_code{
      height: 20px;
      line-height: 20px;
      font-size: 12px;
    }
    
  }

  .list_right{
    float: left;
    width: 15%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    
    // background-color: rgb(186, 97, 208);
  }

}